  @import "src/styles/base.scss";

  :global(.active-nav) {
    cursor: default;
    border-bottom: 2px solid blue;
  }

  .mobile-menu {
    @include sm-hide;
  }

  nav {
    @include sm-show;
  }

  header {
    @include centered;
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }

  .logo {
    width: 200px;

    :global(.active-logo &) {
      cursor: default;
    }
  }
</style>

<header>
  <NavLink url={homePageUrl} activeClass={'active-logo'}>
