<script>
	import { Link } from "svelte-routing";

  export let url = '/';
  export let activeClass = '';
  export let exact = false;

	const getClass = ({ href, isPartiallyCurrent, isCurrent }) => {
    return isCurrent || (exact && isPartiallyCurrent) ? { class: activeClass } : {}
  }
</script>

<Link 
  to='{url}' 
  getProps="{getClass}">
    <slot></slot>
</Link>